.content-main {
	padding-top: 1.5em;
	.row-flex();
	justify-content: space-between;
	.sm-block({
		justify-content: center;
		
	});


	&__left,
	&__right {
		.col();
		.size(12);
		.size-sm(24);
		display: flex;
		flex-direction: column;
		position: relative;
		justify-content: space-between;
		background: @post-bg;
		border-top-left-radius: 30px;
		padding-top: 2rem;
				-webkit-box-shadow: -0.5px 7px 5px -5px rgba(0,0,0,0.75);
		-moz-box-shadow: -0.5px 7px 5px -5px rgba(0,0,0,0.75);
		box-shadow: -0.5px 7px 5px -5px rgba(0,0,0,0.75);
		.sm-block({
			margin-bottom: 10px;
		});
		
	}

	&__title {
		position:relative;
		font-size: 2.2rem;
		line-height: 1;
		color:@blue;
		padding-left: 4rem;

	}

	&__subtitle {
		padding-left: 4rem;
	  font-size: 1.2rem;
	  .arial();
	  line-height: 1.8rem;
	  color: @text;
	  margin-bottom: 2rem;
	  .sm-block({
	  	margin-bottom: 5px;
	  });

	  span {
	  	color: @blue;
	  }

	}

	&__descr {
		padding-left: 4rem;
		.arial();
		color:@text-bold;
		font-size: 1.2rem;
		line-height: 1.8rem;
		.md-block({
			padding-right: 10px;
		});
	}

	&__footer {
		margin-top: 1rem;
		padding: 5px;
		background-color: rgb(244, 243, 243);

			
	}

	&__link {
		.arial();
		display: inline-block;
		float: right;
		margin-right: 1.5rem;
		font-size: 1.2rem;
		font-weight: bold;

		.flaticon-like,
		.flaticon-interface {
			color:@bg;
		}

		span {
			margin-right: 0;
		}
	}
}


