/* Settings */

/* vars */

/* mixins */

/* base-settings */

html {
  font-size: 10px;
}

.container {
  max-width: 960px;
  min-width: 280px;
  padding-left: 10px;
  padding-right: 10px;
  margin: 0 auto;
}

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  font-family: 'League Gothic';
  font-weight: normal;
  font-style: normal;
  background: #ffffff;
  line-height: 1.8rem;
  -webkit-font-smoothing: antialiased;
}

a {
  display: block;
  text-decoration: none;
  line-height: 1.8rem;
}

a:hover {
  text-decoration: none;
}

li {
  list-style: none;
}

ul {
  padding: 0;
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: normal;
}

img {
  max-width: 100%;
}

p {
  margin: 0;
  padding: 0;
}

/* Regular */

@font-face {
  font-family: 'League Gothic';
  src: url('/fonts/leaguegothic-italic-webfont.eot');
  src: url('/fonts/leaguegothic-italic-webfont.eot?#iefix') format('embedded-opentype'), url('/fonts/leaguegothic-italic-webfont.woff') format('woff'), url('/fonts/leaguegothic-italic-webfont.ttf') format('truetype'), url('/fonts/leaguegothic-italic-webfont.svg#league_gothic_italicregular') format('svg');
  font-weight: normal;
  font-style: italic;
}

/* Italic */

@font-face {
  font-family: 'League Gothic';
  src: url('/fonts/leaguegothic-regular-webfont.eot');
  src: url('/fonts/leaguegothic-regular-webfont.eot?#iefix') format('embedded-opentype'), url('/fonts/leaguegothic-regular-webfont.woff') format('woff'), url('/fonts/leaguegothic-regular-webfont.ttf') format('truetype'), url('/fonts/leaguegothic-regular-webfont.svg#league_gothicregular') format('svg');
  font-weight: normal;
  font-style: normal;
}

/* Condensed */

@font-face {
  font-family: 'League Gothic Condensed';
  src: url('/fonts/leaguegothic-condensed-regular-webfont.eot');
  src: url('/fonts/leaguegothic-condensed-regular-webfont.eot?#iefix') format('embedded-opentype'), url('/fonts/leaguegothic-condensed-regular-webfont.woff') format('woff'), url('/fonts/leaguegothic-condensed-regular-webfont.ttf') format('truetype'), url('/fonts/leaguegothic-condensed-regular-webfont.svg#league_gothic_condensed-Rg') format('svg');
  font-weight: normal;
  font-style: normal;
}

/* Condensed Italic */

@font-face {
  font-family: 'League Gothic Condensed';
  src: url('/fonts/leaguegothic-condensed-italic-webfont.eot');
  src: url('/fonts/leaguegothic-condensed-italic-webfont.eot?#iefix') format('embedded-opentype'), url('/fonts/leaguegothic-condensed-italic-webfont.woff') format('woff'), url('/fonts/leaguegothic-condensed-italic-webfont.ttf') format('truetype'), url('/fonts/leaguegothic-condensed-italic-webfont.svg#league_gothic_condensed_itaRg') format('svg');
  font-weight: normal;
  font-style: italic;
}

@font-face {
  font-family: "Flaticon";
  src: url("/fonts/Flaticon.eot");
  src: url("/fonts/Flaticon.eot?#iefix") format("embedded-opentype"), url("/fonts/Flaticon.woff") format("woff"), url("/fonts/Flaticon.ttf") format("truetype"), url("/fonts/Flaticon.svg#Flaticon") format("svg");
  font-weight: normal;
  font-style: normal;
}

[class^="flaticon-"]:before,
[class*=" flaticon-"]:before,
[class^="flaticon-"]:after,
[class*=" flaticon-"]:after {
  font-family: Flaticon;
  font-size: 1.6rem;
  line-height: 1.8;
  font-style: normal;
}

.flaticon-interface:before {
  content: "\f100";
}

.flaticon-like:before {
  content: "\f101";
}

.flaticon-delicious:before {
  content: "\f102";
}

.flaticon-evernote-logo:before {
  content: "\f103";
}

.flaticon-pinterest-logotype-circle:before {
  content: "\f104";
}

.flaticon-linkedin-logo:before {
  content: "\f105";
}

.flaticon-facebook-logo:before {
  content: "\f106";
}

.flaticon-twitter-logo-silhouette:before {
  content: "\f107";
}

/* Blocks */

/* Header */

.header {
  padding-bottom: 1rem;
}

.header__top {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: center;
}

.header__logo {
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(25% - 10px);
  margin-left: calc(37.5% + 5px);
  margin-top: 2.1rem;
  padding-right: 2px;
}

.header__logo .logo {
  font-size: 3.2rem;
  line-height: 1.8rem;
  color: #076e9f;
  text-transform: uppercase;
  text-align: center;
}

.header__logo .logo span {
  color: #22aaec;
}

.header__social {
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(20.833333333333336% - 10px);
  margin-left: calc(16.666666666666668% + 5px);
}

.social {
  text-align: center;
  margin-top: 1rem;
  padding-left: 5.5rem;
}

.social__icon {
  display: inline-block;
  margin-right: 6px;
}

.social__icon:last-child {
  margin-right: 0;
}

.social__link {
  color: #d4d4d4;
  font-size: 1.6rem;
  line-height: 1.8rem;
  transition: all .5s;
}

.social__link:hover {
  color: #707070;
  text-decoration: none;
  transform: translateY(-3px);
}

/* Header-nav */

.header__nav {
  margin-top: 1.8rem;
  text-align: center;
}

.menu {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.menu__item {
  display: flex;
  justify-content: center;
  margin-right: 1.5rem;
}

.menu__link {
  display: block;
  font-size: 1rem;
  font-family: "Arial MT";
  color: #707070;
  text-transform: uppercase;
  padding: 1px 15px;
  transition: all .5s;
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.25);
  border: 1px solid transparent;
}

.menu__link:hover {
  color: #ffffff;
  background: linear-gradient(to top, #066396 0%, #0876a6 100%);
}

.menu__link--active {
  color: #ffffff;
  background: linear-gradient(to top, #066396 0%, #0876a6 100%);
  border: 1px solid #076e9f;
}

/* Main */

.main {
  border-top: 2px solid #22aaec;
}

.main__header {
  background: url('/img/bg/main-img.jpg') repeat-x top center;
  background-size: cover;
  padding-top: 9.2rem;
}

.main__title {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: center;
  margin-bottom: 1.6rem;
}

.main__title .title {
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(29.166666666666668% - 10px);
  text-transform: uppercase;
  color: #076e9f;
  font-size: 6rem;
  text-align: center;
  line-height: 1;
  padding: 10px 20px;
  background: rgba(255, 255, 255, 0.8);
}

.main__title .title span {
  color: #22aaec;
}

.main__subtitle {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: center;
  margin-bottom: 8rem;
}

.main__subtitle .subtitle {
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(83.33333333333334% - 10px);
  font-size: 3rem;
  text-align: center;
  color: #076e9f;
  line-height: 1;
  text-transform: uppercase;
  padding: 15px 0px 13px 0px;
  background: rgba(255, 255, 255, 0.8);
  -moz-transform: matrix(0.98667548, 0, 0, 1, 0, 0);
  -webkit-transform: matrix(0.98667548, 0, 0, 1, 0, 0);
  -ms-transform: matrix(0.98667548, 0, 0, 1, 0, 0);
}

.main__subtitle .subtitle span {
  color: #22aaec;
}

.main__people {
  background: rgba(0, 0, 0, 0.5);
  border-bottom: 2px solid #22aaec;
  padding-bottom: 1.5rem;
  padding-top: 2.5rem;
}

.people {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: space-around;
}

.people__block {
  position: relative;
  background: rgba(255, 255, 255, 0.9);
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(20.833333333333336% - 10px);
  margin-bottom: 1rem;
  border-top-left-radius: 35px;
  border-bottom-left-radius: 35px;
  display: flex;
  justify-content: space-between;
  transition: all .5s;
}

.people__block:hover {
  transform: translateY(-3px);
}

.people__img {
  flex-basis: 100%;
}

.people__img img {
  width: 70px;
  height: 70px;
  border-radius: 50%;
}

.people__text {
  padding-top: 1rem;
}

.people__comment {
  line-height: 1.4rem;
  font-size: 10px;
  font-style: italic;
  color: #515151;
  font-weight: bold;
  font-family: "Arial - Italic MT";
}

.people__data {
  padding-top: 6px;
  font-size: 10px;
  line-height: 1;
  font-family: "Arial MT";
  color: #707070;
}

.main__content {
  background: #ffffff;
}

.main__content .container {
  border-bottom: 2px dotted #d5d5d5;
  /* .sm-block({
				border-bottom: none;
			}); */
}

.main__wrap {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: space-between;
}

.sidebar {
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(20.833333333333336% - 10px);
  font-family: "Arial MT";
  color: #707070;
  border-right: 2px dotted #d5d5d5;
  padding-bottom: 4.5rem;
}

.sidebar__list {
  display: flex;
  flex-direction: column;
}

.sidebar__block {
  border-bottom: 2px dotted #d5d5d5;
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
}

.sidebar__block:last-child {
  border: none;
}

.sidebar__title {
  font-family: 'League Gothic';
  font-weight: normal;
  font-style: normal;
  text-transform: uppercase;
  color: #076e9f;
  font-size: 2rem;
}

.sidebar__title--author {
  margin-bottom: 1.5rem;
  margin-top: 1.5rem;
}

.sidebar__title--post {
  margin-bottom: 1.4rem;
}

.sidebar__title--currency {
  margin-bottom: 1rem;
  margin-top: 7px;
}

.sidebar__content {
  font-size: 10px;
  color: #707070;
  line-height: 1.8rem;
  padding-right: 2.4rem;
}

.sidebar__content span {
  font-weight: bold;
  color: #076e9f;
}

.post {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.post__item {
  flex-basis: 100%;
  margin-bottom: 2rem;
}

.post__pic {
  float: left;
}

.post__pic img {
  width: 50px;
  height: 40px;
}

.post__title {
  padding-left: 30px;
  float: left;
  text-transform: uppercase;
  font-family: "Arial - Bold MT";
  font-weight: 700;
  font-size: 10px;
}

.post__comment {
  display: inline-block;
  color: #707070;
  text-align: center;
  font-style: italic;
  font-size: 1.2rem;
  padding-left: 13px;
  margin-top: -7px;
}

.post__comment:hover {
  color: #515151;
}

.post__icon {
  color: #dddddd;
}

/* Table */

.table {
  font-size: 10px;
  text-align: center;
  border: 1px solid black;
}

.table__header {
  text-align: center;
  background: #076e9f;
  color: #ffffff;
  padding: 5px 9px 5px 7px;
  border: 1px solid #ffffff;
}

.table__data {
  color: #076e9f;
  padding: 4px 10px 0px 7px;
  border: 1px solid #ffffff;
  background: #c7e9fa;
}

.table__data--colored {
  background: #b1e1f8;
}

.content {
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(79.16666666666667% - 10px);
  position: relative;
  bottom: 15px;
}

.content-header {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  background: #ffffff;
  border-top: 2px solid #22aaec;
  padding: 4rem 0 2.5rem 0.7rem;
  border-right: 2px dotted #d5d5d5;
  border-bottom: 2px dotted #d5d5d5;
}

.content-header__title {
  flex-basis: 100%;
  font-size: 3.2rem;
  color: #076e9f;
  text-transform: uppercase;
  line-height: 1.4rem;
  margin-bottom: 2rem;
}

.content-header__left {
  flex-basis: 20%;
}

.content-header__right {
  flex-basis: 80%;
}

.content-header__logo {
  display: inline-block;
  width: 5.3rem;
  height: 5.3rem;
  background: #22aaec;
  border-radius: 50%;
  margin-right: 5px;
}

.content-header__logo span {
  font-size: 1rem;
  font-family: "Arial - Bold MT";
  font-weight: 700;
  text-align: center;
  color: #ffffff;
  display: block;
  margin: 0 auto;
  padding-top: 2rem;
  width: 80%;
  line-height: 1;
}

.content-header__text {
  font-size: 1.2rem;
  font-family: "Arial MT";
  color: #707070;
  line-height: 1.8rem;
  padding-bottom: 3.2rem;
}

.content-header__btn {
  font-size: 11px;
  font-family: "Arial MT";
  line-height: 1.8rem;
  display: inline-block;
  color: #ffffff;
  margin: 0 auto;
  margin-top: -5px;
  margin-left: 30px;
  text-align: center;
  padding: 2px 8px;
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.25), 0 2px 0 rgba(0, 0, 0, 0.16);
  border: 1px solid #045279;
  background-color: #076e9f;
  background-image: -webkit-linear-gradient(bottom, rgba(0, 0, 0, 0.2) 0%, rgba(255, 255, 255, 0.2) 100%);
  background-image: -o-linear-gradient(bottom, rgba(0, 0, 0, 0.2) 0%, rgba(255, 255, 255, 0.2) 100%);
  background-image: linear-gradient(to top, rgba(0, 0, 0, 0.2) 0%, rgba(255, 255, 255, 0.2) 100%);
  border-bottom: 3px solid #00141e;
  transition: all .5s;
}

.content-header__btn:hover {
  background-color: #90d4f5;
  color: #ffffff;
}

.content__social {
  z-index: 99;
  position: absolute;
  right: -7px;
  bottom: 20px;
  background: #90d4f5;
  padding-right: 8px;
  padding-left: 8px;
}

.content__social:after {
  z-index: 9;
  content: '';
  position: absolute;
  display: block;
  top: 27px;
  right: 0;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 5px 5px 0 0;
  border-color: #0075e9 transparent transparent transparent;
}

.content__link {
  font-weight: bold;
  font-family: "Arial MT";
  display: inline-block;
  font-size: 1.2rem;
  line-height: 1.8rem;
  margin-right: 1.5rem;
}

.content__link:last-child {
  margin-right: 0;
}

.content__link .flaticon-like {
  margin-left: 1rem;
  color: #ffffff;
}

.content__link .flaticon-interface {
  color: #ffffff;
}

.content__link:hover {
  color: #076e9f;
}

.content-main {
  padding-top: 1.5em;
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: space-between;
}

.content-main__left,
.content-main__right {
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(50% - 10px);
  display: flex;
  flex-direction: column;
  position: relative;
  justify-content: space-between;
  background: #eaeaea;
  border-top-left-radius: 30px;
  padding-top: 2rem;
  -webkit-box-shadow: -0.5px 7px 5px -5px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: -0.5px 7px 5px -5px rgba(0, 0, 0, 0.75);
  box-shadow: -0.5px 7px 5px -5px rgba(0, 0, 0, 0.75);
}

.content-main__title {
  position: relative;
  font-size: 2.2rem;
  line-height: 1;
  color: #076e9f;
  padding-left: 4rem;
}

.content-main__subtitle {
  padding-left: 4rem;
  font-size: 1.2rem;
  font-family: "Arial MT";
  line-height: 1.8rem;
  color: #707070;
  margin-bottom: 2rem;
}

.content-main__subtitle span {
  color: #076e9f;
}

.content-main__descr {
  padding-left: 4rem;
  font-family: "Arial MT";
  color: #515151;
  font-size: 1.2rem;
  line-height: 1.8rem;
}

.content-main__footer {
  margin-top: 1rem;
  padding: 5px;
  background-color: #f4f3f3;
}

.content-main__link {
  font-family: "Arial MT";
  display: inline-block;
  float: right;
  margin-right: 1.5rem;
  font-size: 1.2rem;
  font-weight: bold;
}

.content-main__link .flaticon-like,
.content-main__link .flaticon-interface {
  color: #90d4f5;
}

.content-main__link span {
  margin-right: 0;
}

.content-footer {
  display: flex;
  justify-content: center;
  padding: 3rem;
  margin-top: 2rem;
  background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, #000000 100%);
  background: #eaeaea;
  -webkit-box-shadow: -0.5px 7px 5px -5px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: -0.5px 7px 5px -5px rgba(0, 0, 0, 0.75);
  box-shadow: -0.5px 7px 5px -5px rgba(0, 0, 0, 0.75);
}

.content-footer p {
  text-align: center;
  font-size: 10px;
  text-transform: uppercase;
  font-family: "Arial MT";
}

.map__title {
  text-transform: uppercase;
  color: #076e9f;
  font-size: 3.2rem;
  line-height: 1.2;
  margin-left: 5px;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.map__block {
  border-top: 1px solid #eaeaea;
  border-bottom: 1px solid #eaeaea;
  width: 100%;
  background-size: cover;
  min-height: 203px;
  background: url('../img/bg/map.jpg') repeat-x center center;
}

/* Footer */

.footer__top {
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  justify-content: space-between;
  border-bottom: 2px dotted #d4d4d4;
}

.footer__logo {
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(12.5% - 10px);
}

.footer__logo .logo {
  padding-left: 6px;
  text-transform: uppercase;
  font-size: 1.6rem;
  line-height: 1.8rem;
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
}

.footer__logo .logo span {
  color: #22aaec;
  margin-bottom: 0;
}

.footer__menu {
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(29.166666666666668% - 10px);
  margin-top: 1.4rem;
  display: flex;
  justify-content: space-between;
}

.footer__link {
  font-size: 10px;
  font-family: "Arial MT";
  color: #707070;
  text-transform: uppercase;
}

.footer__copyright {
  text-align: center;
  font-size: 10px;
  line-height: 1.8rem;
  font-family: "Arial MT";
  padding: 6px 0;
  text-transform: uppercase;
}

/* Adaptive */

@media screen and (max-width: 992px) {
  .container {
    padding-left: 10px;
    padding-right: 10px;
