.content-footer {
	display: flex;
	justify-content: center;
	padding: 3rem;
	margin-top: 2rem;
	background: linear-gradient( 90deg, rgba(255,255,255,0) 0%, rgb(0,0,0) 100%);
	background: @post-bg;
	.sm-block({
		margin-top: 0;
		
	});

	-webkit-box-shadow: -0.5px 7px 5px -5px rgba(0,0,0,0.75);
		-moz-box-shadow: -0.5px 7px 5px -5px rgba(0,0,0,0.75);
		box-shadow: -0.5px 7px 5px -5px rgba(0,0,0,0.75);

	p {
		text-align: center;
		font-size: 10px;
		text-transform: uppercase;
		.arial();
	}
}
