/* Footer */


.footer {

	&__top {
		.row-flex();
		justify-content: space-between;
		border-bottom: 2px dotted @light-gray;
		.sm-block({
			justify-content: center;
		});
	}

	&__logo {
		.col();
		.size(3);
		.size-md(6);
		.size-sm(24);

		.logo {
			padding-left: 6px;
			text-transform: uppercase;
			font-size: 1.6rem;
			line-height: 1.8rem;
			margin-top: 1.5rem;
			margin-bottom: 1.5rem;

			span {
				color:@light-blue;
				margin-bottom: 0;
			}

			.sm-block({
				font-size: 2rem;
				text-align: center;
				padding-left: 0;
				margin-top: 5px;
				margin-bottom: 5px;
			});
		}
		
	}

	&__menu {
		.col();
		.size(7);
		.size-md(6);
		.size-sm(24);
		margin-top: 1.4rem;
		display: flex;
		justify-content: space-between;

		.sm-block({
			margin-top: 0;
			justify-content: center;
			margin-bottom: 5px;
		});
	}

	&__item {
		.sm-block({
			margin-right: 10px;
		});
	}


	&__link {
		font-size: 10px;
		.arial();
		color: @text;
		text-transform: uppercase;
	}

	&__copyright {
		text-align: center;
		font-size: 10px;
		line-height: 1.8rem;
		.arial();
		padding: 6px 0;
		text-transform: uppercase;
	}
}