/* Main */

.main {	
	border-top: 2px solid @light-blue;
	
	
	&__header {
		background: url('/img/bg/main-img.jpg') repeat-x top center;
		background-size: cover;
		padding-top: 9.2rem;
		.sm-block({
			padding-top: 6rem;
			
		});
		.xs-block({
			padding-top: 3rem;
			
		});

	}

	&__title {
		.row-flex();
		justify-content: center;
		margin-bottom: 1.6rem;

		.title {
			.col();
			.size(7);
			.size-md(9);
			.size-sm(12);
			.size-xs(20);
			text-transform: uppercase;
			color: @blue;
			font-size: 6rem;
			text-align: center;
			line-height: 1;
			padding: 10px 20px;
			background: rgba(255, 255, 255, .8);
		
			span {
				color: @light-blue;
			} 
		}
	}

	&__subtitle {
		.row-flex();
		justify-content: center;
		margin-bottom: 8rem;

		.subtitle {
			.col();
			.size(20);
			.size-xs(24);
			.sm-block({
				padding: 10px 5px;
			});

				font-size: 3rem;
		    	text-align: center;
		    	color: @blue;
		    	line-height: 1;
		    	text-transform: uppercase;
		    	padding: 15px 0px 13px 0px;
		    	background: rgba(255, 255, 255, 0.8);
		    	-moz-transform: matrix( 0.98667547626913,0,0,1,0,0);
		  		-webkit-transform: matrix( 0.98667547626913,0,0,1,0,0);
		  		-ms-transform: matrix( 0.98667547626913,0,0,1,0,0);
		
			span {
				color: @light-blue;
			}
		}
		
	}

	&__people {
		background: rgba(0, 0, 0, .5);
		border-bottom: 2px solid @light-blue;
		padding-bottom: 1.5rem;
		padding-top: 2.5rem;
		.sm-block({
			padding-top: 15px;
			padding-bottom: 15px;
		});
		.xs-block({
			padding-top: 10px;
			padding-bottom: 10px;
			
		});

	}
}

.people {
	.row-flex();
	justify-content: space-around;


	.sm-block({
		justify-content: center;
		
	});

	&__block {
		position: relative;
		background: rgba(255, 255, 255, .9);
		.col();
		.size(5);
		.size-md(6);
		.size-sm(8);
		.size-xs(14);
		margin-bottom: 1rem;
		border-top-left-radius: 35px;
		border-bottom-left-radius: 35px;
		display: flex;
		justify-content: space-between;
		.trs();
		
		&:hover {
			.trf();
		}
	}

	 &__img {
	 	flex-basis: 100%;

		img {
			width: 70px;
			height: 70px;
			border-radius: 50%;
		}
			
	}

	&__text {
		padding-top: 1rem;

		.sm-block({
			padding-left: 1rem;
			padding-top: 1rem;
			padding-top: 1rem;
		});
	}

	&__comment {
		line-height: 1.4rem;
		font-size: 10px;
		font-style: italic;
		color: @text-bold;
		font-weight: bold;
		font-family: "Arial - Italic MT";
	}

	&__data {
		padding-top: 6px;
		font-size: 10px;
		line-height: 1;
		font-family: "Arial MT";
		color: @text;
		
	}

}

