.map {
	&__title {
		text-transform: uppercase;
		color: @blue;
		font-size: 3.2rem;
		line-height: 1.2;
		margin-left: 5px;
		padding-top: 1rem;
		padding-bottom: 1rem;
		.sm-block({
			text-align: center;
			margin: 0;
			
		});
	}

	&__block {
		border-top: 1px solid #eaeaea;
		border-bottom: 1px solid #eaeaea;
		width: 100%;
		background-size: cover;
		min-height: 203px;
		background: url('../img/bg/map.jpg') repeat-x center center;
	}
}