.main {

	&__content {
		background: @white;
		

		.container {
			border-bottom: 2px dotted #d5d5d5;
			/* .sm-block({
				border-bottom: none;
			}); */
		}

	}

	&__wrap {
		.row-flex();
		justify-content: space-between;
	}
}



.sidebar {
	.col();
	.size(5);
	.size-md(8);
	.size-sm(24);
	.arial();
	color: @text;
	border-right: 2px dotted #d5d5d5;
	padding-bottom: 4.5rem;
	.sm-block({
		padding-bottom: 0;
		border-right: none;
	});

	&__list {
		display: flex;
		flex-direction: column;
	}

	&__block {	
		border-bottom: 2px dotted #d5d5d5;
		padding-top: 1.5rem;
		padding-bottom: 1.5rem;

		&:last-child {
			border: none;
		}
	}

	&__title {
		.font();
		text-transform: uppercase;
		color: @blue;
		font-size: 2rem;

		&--author {
			margin-bottom: 1.5rem;
			margin-top: 1.5rem;
		}

		&--post {
			margin-bottom: 1.4rem;
		}

		&--currency {
			margin-bottom: 1rem;
			margin-top: 7px;
		}

		.sm-block({
			text-align: center;
			margin-bottom: 5px;
			margin-top: 5px;
		});

		

	}

	&__content {
		font-size: 10px;
		color: @text;
		
		line-height: 1.8rem;
		padding-right: 2.4rem;
		.sm-block({
			padding-right: 0;
		});

		span {
			font-weight: bold;
			color: @blue;
		}
	}

}


.post {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;

	&__item {
		flex-basis: 100%;
		margin-bottom: 2rem;
		.sm-block({
			flex-basis: 200px;
			max-width: 200px;
			display: flex;
			flex-wrap: wrap;
			margin: 5px;
			justify-content: center;
		});
	}

	&__pic {
		float: left;

		img {
			width: 50px;
			height: 40px;

			.sm-block({
				flex-basis: 100%;
				width: 200px;
				height: 200px;
				text-align: center;
			});
		}
	}

	&__title {
		padding-left: 30px;
		float: left;
		text-transform: uppercase;
		.bold();
		font-size: 10px;
		.sm-block({
			font-size: 16px;
			order:-1;
			text-align: center;
			margin-top: 5px;
			margin-bottom: 5px;
			padding: 0;
			flex-basis: 100%;
		});
	}

	&__comment {
		display: inline-block;
		color: @text;
		text-align: center;
		font-style: italic;
		font-size: 1.2rem;
		padding-left: 13px;
		margin-top: -7px;

		&:hover {
			color: @text-bold;
		}
		.sm-block({
			font-size: 14px;
			flex-basis: 100%;
			text-align: center;
			padding: 0;
			margin-top: 5px;
		});
	}

	&__icon {
		color: @icon-color;
	}
}

/* Table */

.table {

	font-size: 10px;
	text-align: center;
	border: 1px solid black;
	.sm-block({
		margin: 0 auto;
		width: 100%;
		font-size: 14px;
		border: 2px solid black;
		
	});
    
	&__header {
		text-align: center;
		background: @blue;
		color: @white;
		padding: 5px 9px 5px 7px;
		border: 1px solid @white;

		.sm-block({
			padding: 10px 20px 10px 15px;
			border: 2px solid @white;
		});

	}

	&__data {
		color: @blue;
		padding: 4px 10px 0px 7px;
		border: 1px solid @white;
		background: #c7e9fa;

		.sm-block({
			border-width: 2px;
			padding: 10px 20px 10px 15px;
		});

		&--colored {
			background: #b1e1f8;
		}
	}
}