

@font-face {
  font-family: "Flaticon";
  src: url("/fonts/Flaticon.eot");
  src: url("/fonts/Flaticon.eot?#iefix") format("embedded-opentype"),
       url("/fonts/Flaticon.woff") format("woff"),
       url("/fonts/Flaticon.ttf") format("truetype"),
       url("/fonts/Flaticon.svg#Flaticon") format("svg");
  font-weight: normal;
  font-style: normal;
}

@media screen and (-webkit-min-device-pixel-ratio:0) {
  @font-face {
    font-family: "Flaticon";
    src: url("/fonts/Flaticon.svg#Flaticon") format("svg");
  }
}

[class^="flaticon-"]:before, [class*=" flaticon-"]:before,
[class^="flaticon-"]:after, [class*=" flaticon-"]:after {   
        font-family: Flaticon;
        font-size: 1.6rem;
        line-height: 1.8;
        font-style: normal;
}

.flaticon-interface:before { content: "\f100"; }
.flaticon-like:before { content: "\f101"; }
.flaticon-delicious:before { content: "\f102"; }
.flaticon-evernote-logo:before { content: "\f103"; }
.flaticon-pinterest-logotype-circle:before { content: "\f104"; }
.flaticon-linkedin-logo:before { content: "\f105"; }
.flaticon-facebook-logo:before { content: "\f106"; }
.flaticon-twitter-logo-silhouette:before { content: "\f107"; }