/* Header */

.header {
	padding-bottom: 1rem;
	.sm-block({
			padding-bottom: 0;
		});

	&__top {
		.row-flex();
		justify-content: center;
	}

	&__logo {
		.col();
		.size(6);
		.size-sm(12);
		.size-xs(24);
		.offset-left(9);
		.offset-left-sm(0);
		margin-top: 2.1rem;
		padding-right: 2px;
		.sm-block({
			text-align:left;
			});

		.logo {
			font-size: 3.2rem;
			line-height: 1.8rem;
			color: @blue;
			text-transform: uppercase;
			text-align: center;

				span {
					color:@light-blue;
			}
		}
	}

	&__social {
		.col();
		.size(5);
		.size-md(7);
		.size-sm(12);
		.size-xs(24);
		.offset-left(4);
		.offset-left-md(2);
		.offset-left-sm(0);
	}

}

.social {
	text-align: center;
	margin-top: 1rem;
	padding-left: 5.5rem;
	.sm-block({
		padding-left: 0;
		
	});


	&__icon {
		display: inline-block;
		margin-right: 6px;
		.sm-block({
			margin-right: 12px;
			
		});

		&:last-child {
			margin-right: 0;
		}
	}


	&__link {
		color: @light-gray;
		font-size: 1.6rem;
		line-height: 1.8rem;
		.trs();

		&:hover {
			color: @text;
			text-decoration: none;
			.trf();
		}
	}
}

/* Header-nav */

.header__nav {
	margin-top: 1.8rem;
	text-align: center;
	.sm-block({
		padding-bottom: 10px;
		margin-top: 10px;
		
	});
}

.menu {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;

	&__item {
		display: flex;
		justify-content: center;
		margin-right: 1.5rem;
		.sm-block({
			margin: 0;
		});
	}


	&__link {
		display: block;
		font-size: 1rem;
		.arial();
		color: @text;
		text-transform: uppercase;
		padding: 1px 15px;
		.trs();
		box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.25);
		border: 1px solid transparent;
		&:hover {
			color: @white;
			background: linear-gradient(to top, #066396 0%, #0876a6 100%);

		}

		&--active {
			color: @white;
			background: linear-gradient(to top, #066396 0%, #0876a6 100%);
			border: 1px solid @blue;
		}
	}
}